<template>
  <v-container grid-list-xs>
    <v-overlay :value="overlay">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
      <v-btn icon @click="overlay = false">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-overlay>
    <v-overlay :value="overlayUnclosed">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
    </v-overlay>
    <v-dialog
      transition="dialog-top-transition"
      max-width="600"
      v-model="carryForwardDialog"
    >
      <v-card>
        <v-toolbar
          color="primary"
          dark
        >Requesting Leave Carry Forward</v-toolbar>
        <v-card-text>
          Are you sure that you want to request carrying {{ annualLeave.balanceCarried }} days from leave cycle {{ annualLeave.previousPeriod }} to leave cycle {{ annualLeave.currentPeriod }}
        </v-card-text>
        <v-card-actions class="justify-end">
          <v-btn @click="carryForwardDialog = false" color="error">Cancel</v-btn>
          <v-spacer></v-spacer>
          <v-btn @click="createCarryForwardRequest">Yes, Proceed</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      transition="dialog-top-transition"
      max-width="600"
      v-model="carryForwardActionDialog"
    >
      <v-card>
        <v-toolbar
          color="primary"
          dark
        >Requesting Leave Carry Forward</v-toolbar>
        <v-card-text>
          Select action for carrying {{ annualLeave.balanceCarried }} days from leave cycle {{ annualLeave.previousPeriod }} to leave cycle {{ annualLeave.currentPeriod }}
          <v-select
            :items="carryRequestActions"
            v-model="carryRequestAction"
            label="Select Action"
          ></v-select>
        </v-card-text>
        <v-card-actions class="justify-end">
          <v-btn @click="carryForwardActionDialog = false" color="error">Cancel</v-btn>
          <v-spacer></v-spacer>
          <v-btn :disabled="!carryRequestAction" @click="createCarryForwardAction">Proceed and Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-navigation-drawer
      app
      class="primary darken-1 white--text"
      clipped
      permanent
      right
      style="z-index: 3;"
    >
      <v-list class="white--text">
        <v-list-item>
          <v-btn class="secondary" dark @click="$router.push('/resource/view/practitioner/' + practitionerId)">
            <v-icon light left>mdi-eye</v-icon>
            <span>View Health Profession</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canRequestCarryForward">
          <v-btn
            class="primary"
            dark
            small
            @click="carryForwardDialog = true"
          >
            <v-icon light left>mdi-plus</v-icon>
            <span>Request Leave Carry Forward</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canApproveRequestCarryForward">
          <v-btn
            class="primary"
            dark
            small
            @click="carryForwardActionDialog = true"
          >
            <v-icon light left>mdi-plus</v-icon>
            <span>Approve Carry Forward Request</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canAddSupervisorApproval">
          <v-btn
            class="primary"
            dark
            small
            @click="$router.push('/questionnaire/supervisor-leave-approval/supervisor-leave-approval?practitioner=' + practitionerId + '&leaverequest=' + queries.id)"
          >
            <v-icon light left>mdi-plus</v-icon>
            <span>Add Approval</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canAddSupervisorSupervisorApproval">
          <v-btn
            class="primary"
            dark
            small
            @click="$router.push('/questionnaire/supervisor-supervisor-leave-approval/supervisor-supervisor-leave-approval?practitioner=' + practitionerId + '&leaverequest=' + queries.id)"
          >
            <v-icon light left>mdi-plus</v-icon>
            <span>Add Approval</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canAddHRApproval">
          <v-btn
            class="primary"
            dark
            small
            @click="$router.push('/questionnaire/hr-leave-approval/hr-leave-approval?practitioner=' + practitionerId + '&leaverequest=' + queries.id)"
          >
            <v-icon light left>mdi-plus</v-icon>
            <span>Add Approval</span>
          </v-btn>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
    <v-row>
      <v-col>
        <v-data-table
          :headers="leaveDetailsHeaders"
          :items="leaveDetails"
          :items-per-page="10"
          class="elevation-1"
        ></v-data-table>
        <br>
        <v-card 
          class="mx-auto"
          max-width="700"
          outlined
          v-if="carryForwardRequest.fromPeriod"
        >
          <v-card-title class="primary darken-1 white--text text-uppercase font-weight-bold">
            Leave Carry Forward Request
          </v-card-title>
          <v-card-text class="my-3">
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">From Leave Cycle</span>
              </v-col>
              <v-col cols="8">
                {{ carryForwardRequest.fromPeriod }}
              </v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">To Leave Cycle</span>
              </v-col>
              <v-col cols="8">
                {{ carryForwardRequest.toPeriod }}
              </v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Request Date</span>
              </v-col>
              <v-col cols="8">
                {{ carryForwardRequest.requestDate }}
              </v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Status</span>
              </v-col>
              <v-col cols="8">
                {{ carryForwardRequest.status.display }}
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
        <v-card 
          class="mx-auto"
          max-width="700"
          outlined
          v-if="leaveRequest.category"
          >
          <v-card-title class="primary darken-1 white--text text-uppercase font-weight-bold">
            Leave Request
          </v-card-title>
          <v-card-text class="my-3">
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Leave Category</span>
              </v-col>
              <v-col cols="8">
                {{ leaveRequest.category }}
              </v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Start Date</span>
              </v-col>
              <v-col cols="8">
                {{ leaveRequest.startDate }}
              </v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">End Date</span>
              </v-col>
              <v-col cols="8">
                {{ leaveRequest.endDate }}
              </v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Return Date</span>
              </v-col>
              <v-col cols="8">
                {{ leaveRequest.returnDate }}
              </v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Days</span>
              </v-col>
              <v-col cols="8">
                {{ leaveRequest.days }}
              </v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Status</span>
              </v-col>
              <v-col cols="8">
                {{ leaveRequest.status }}
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
        <v-card 
          class="mx-auto"
          max-width="700"
          outlined
          v-if="supervisorApproval.id"
          >
          <v-card-title class="accent darken-1 white--text text-uppercase font-weight-bold">
            Immediate Supervisor Approval
          </v-card-title>
          <v-card-text class="my-3">
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Action</span>
              </v-col>
              <v-col cols="8">
                {{ supervisorApproval.status }}
              </v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Action Date</span>
              </v-col>
              <v-col cols="8">
                {{ supervisorApproval.date }}
              </v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Description</span>
              </v-col>
              <v-col cols="8">
                {{ supervisorApproval.description }}
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
        <v-card 
          class="mx-auto"
          max-width="700"
          outlined
          v-if="supervisorSupervisorApproval.id"
          >
          <v-card-title class="accent darken-1 white--text text-uppercase font-weight-bold">
            Supervisor's Supervisor Approval
          </v-card-title>
          <v-card-text class="my-3">
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Action</span>
              </v-col>
              <v-col cols="8">
                {{ supervisorSupervisorApproval.status }}
              </v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Action Date</span>
              </v-col>
              <v-col cols="8">
                {{ supervisorSupervisorApproval.date }}
              </v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Description</span>
              </v-col>
              <v-col cols="8">
                {{ supervisorSupervisorApproval.description }}
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
        <v-card 
          class="mx-auto"
          max-width="700"
          outlined
          v-if="hrApproval.id"
          >
          <v-card-title class="accent darken-1 white--text text-uppercase font-weight-bold">
            HR Approval
          </v-card-title>
          <v-card-text class="my-3">
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Action</span>
              </v-col>
              <v-col cols="8">
                {{ hrApproval.status }}
              </v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Action Date</span>
              </v-col>
              <v-col cols="8">
                {{ hrApproval.date }}
              </v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Description</span>
              </v-col>
              <v-col cols="8">
                {{ hrApproval.description }}
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col cols="3">
        <br>
        <leave-request-stepper
          :step="step"
          :noSupervisorAppr="!supervisorApproval.id"
          :noSupervisorSupervisorAppr="!supervisorSupervisorApproval.id"
          :noHRApproval="!hrApproval.id"
          :status="leaveRequest.status"
        />
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
const querystring = require("querystring");
export default {
  props: ["queries"],
  data() {
    return {
      overlay: false,
      overlayUnclosed: false,
      loading: false,
      carryForwardDialog: false,
      carryForwardActionDialog: false,
      practitionerId: '',
      leaveRequest: {
        startDate: "",
        category: ""
      },
      supervisorApproval: {
        id: ""
      },
      supervisorSupervisorApproval: {
        id: ""
      },
      hrApproval: {
        id: ""
      },
      carryForwardRequest: {
        id: "loading",
        status: {}
      },
      noSupervisor: false,
      noSupervisorSupervisor: false,
      supervisor: "",
      supervisorSupervisor: "",
      step: 1,
      leaveDetails: [],
      annualLeave: {},
      leaveDetailsHeaders: [{
        text: "Period",
        value: "period"
      }, {
        text: "Category",
        value: "category"
      }, {
        text: "Leave Days",
        value: "maxDays"
      }, {
        text: "Accrued",
        value: "accrued"
      }, {
        text: "Days on Leave",
        value: "daysOnLeave"
      }, {
        text: "Expired",
        value: "expired"
      }, {
        text: "Balance",
        value: "balance"
      }],
      carryRequestActions: [{
        text: "Pending",
        value: "pending"
      }, {
        text: "Approve",
        value: "approved"
      }, {
        text: "Reject",
        value: "rejected"
      }],
      carryRequestAction: ""
    }
  },
  computed: {
    canAddSupervisorApproval() {
      let userPractitioner = this.$store.state.user.obj.resource.extension.find((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference"
      })
      if(userPractitioner && userPractitioner.valueReference.reference === this.supervisor && !this.supervisorApproval.id) {
        return true
      }
      return false
    },
    canAddSupervisorSupervisorApproval() {
      let userPractitioner = this.$store.state.user.obj.resource.extension.find((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference"
      })
      if(userPractitioner && userPractitioner.valueReference.reference === this.supervisorSupervisor && this.supervisorApproval.id && !this.supervisorSupervisorApproval.id) {
        return true
      }
      return false
    },
    canAddHRApproval() {
      let role = this.$store.state.user.obj.resource.extension.find((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-assign-role"
      })
      if(role && role.valueReference.reference === "Basic/na-hr-manager" && this.supervisorSupervisorApproval.id && !this.hrApproval.id) {
        return true
      }
      return false
    },
    canRequestCarryForward() {
      if((this.carryForwardRequest.id && this.carryForwardRequest.id !== 'loading') || this.carryForwardRequest.id === 'loading') {
        return false
      }
      if(this.leaveDetails.length) {
        let startPeriod = this.annualLeave.currentPeriod.split("/")[0]
        let months = this.$moment().diff(startPeriod, 'months')
        if(months > 6 || this.annualLeave.balanceCarried <= 0 || this.annualLeave.balanceCarried <= this.annualLeave.expired) {
          return false
        } else if(this.annualLeave.maxDays) {
          return true
        }
      }
      return false
    },
    canApproveRequestCarryForward() {
      if(!this.carryForwardRequest.id || this.carryForwardRequest.id === 'loading') {
        return false
      }
      let role = this.$store.state.user.obj.resource.extension.find((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-assign-role"
      })
      if(role && role.valueReference.reference === "Basic/exec-manager" && this.carryForwardRequest.status.code !== 'approved') {
        return true
      }
      return false
    }
  },
  methods: {
    getLeaveCaryForward() {
      let query = {
        practitioner: "Practitioner/" + this.practitionerId,
        _profile: "http://ihris.org/fhir/StructureDefinition/leave-carry-forward-request-profile",
        toleaveperiod: this.annualLeave.currentPeriod
      }
      fetch("/fhir/Basic?" + querystring.stringify(query)).then(response => response.json().then(async(response) => {
        if(response && response.entry && response.entry.length) {
          this.populateCarryForward(response.entry[0].resource)
        } else {
          this.carryForwardRequest.id = ""
        }
      }))
    },
    populateCarryForward(carry) {
      this.carryForwardRequest.id = carry.id
      this.carryForwardRequest.fromPeriod = carry.extension.find((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/from-leave-period"
      }).valueString
      this.carryForwardRequest.toPeriod = carry.extension.find((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/to-leave-period"
      }).valueString
      this.carryForwardRequest.requestDate = carry.extension.find((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/request-date"
      }).valueDate
      this.carryForwardRequest.status = carry.extension.find((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/carry-approval-status"
      }).valueCoding
    },
    getLeaveDetails() {
      fetch("/site/leave/balance/" + this.practitionerId).then(response => response.json().then((response) => {
        this.leaveDetails = response
        this.annualLeave = this.leaveDetails.find((leave) => {
          return leave.category === "Annual"
        })
        this.getLeaveCaryForward()
      }))
    },
    getSupervisor() {
      let query = {
        practitioner: this.practitionerId,
        _count: 200,
        _sort: "-_id"
      }
      let role
      fetch("/fhir/PractitionerRole?" + querystring.stringify(query)).then(response => response.json().then(async(response) => {
        if(response && response.entry && response.entry.length) {
          for(let entry of response.entry) {
            if(!role) {
              role = entry
            } else if(role.resource.id < entry.resource.id) {
              role = entry
            }
          }
        }
        let supervisor = role.resource.extension.find((ext) => {
          return ext.url === "http://ihris.org/fhir/StructureDefinition/supervisor"
        })
        if(!supervisor) {
          this.noSupervisor = true
        } else {
          this.supervisor = supervisor.valueReference.reference
          this.getSupervisorSupervisor(this.supervisor)
        }
      }))
    },
    getSupervisorSupervisor(practitioner) {
      let query = {
        practitioner: practitioner,
        _count: 200,
        _sort: "-_id"
      }
      let role
      fetch("/fhir/PractitionerRole?" + querystring.stringify(query)).then(response => response.json().then(async(response) => {
        if(response && response.entry && response.entry.length) {
          for(let entry of response.entry) {
            if(!role) {
              role = entry
            } else if(role.resource.id < entry.resource.id) {
              role = entry
            }
          }
        }
        let supervisor = role.resource.extension.find((ext) => {
          return ext.url === "http://ihris.org/fhir/StructureDefinition/supervisor"
        })
        if(!supervisor) {
          this.noSupervisorSupervisor = true
        } else {
          this.supervisorSupervisor = supervisor.valueReference.reference
        }
      }))
    },
    getLeave() {
      fetch("/fhir/Basic/" + this.queries.id).then(response => response.json().then(async(leave) => {
        this.practitionerId = leave.extension.find((ext) => {
          return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference"
        }).valueReference.reference
        this.practitionerId = this.practitionerId.split("/")[1]
        this.leaveRequest.startDate = leave.extension.find((ext) => {
          return ext.url === "http://ihris.org/fhir/StructureDefinition/start-date"
        }).valueDate
        this.leaveRequest.endDate = leave.extension.find((ext) => {
          return ext.url === "http://ihris.org/fhir/StructureDefinition/end-date"
        }).valueDate
        this.leaveRequest.returnDate = leave.extension.find((ext) => {
          return ext.url === "http://ihris.org/fhir/StructureDefinition/return-date"
        }).valueDate
        this.leaveRequest.days = leave.extension.find((ext) => {
          return ext.url === "http://ihris.org/fhir/StructureDefinition/leave-days"
        })?.valueInteger
        this.leaveRequest.status = leave.extension.find((ext) => {
          return ext.url === "http://ihris.org/fhir/StructureDefinition/leave-status"
        }).valueCoding.display
        if(this.leaveRequest.status === "Approved") {
          this.step = 4
        }
        let category = leave.extension.find((ext) => {
          return ext.url === "http://ihris.org/fhir/StructureDefinition/leave-category"
        }).valueReference.reference
        await fetch("/fhir/" + category).then(response => response.json().then(async(category) => {
          this.leaveRequest.category = category.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-basic-name"
          }).valueString
        }))
        this.getLeaveDetails()
        this.getSupervisor()
        this.getSupervisorApproval()
      }))
    },
    getSupervisorApproval() {
      let query = {
        _profile: 'http://ihris.org/fhir/StructureDefinition/supervisor-leave-approval-profile',
        leaverequest: 'Basic/' + this.queries.id,
        _sort: '_lastUpdated'
      }
      fetch("/fhir/Basic?" + querystring.stringify(query)).then(response => response.json().then((appr) => {
        if(appr.entry && appr.entry.length) {
          if(this.step != 4) {
            this.step=2
          }
          this.supervisorApproval.id = appr.entry[0].resource.id
          this.supervisorApproval.status = appr.entry[0].resource.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/supervisor-leave-status"
          }).valueCoding.display
          this.supervisorApproval.date = appr.entry[0].resource.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/action-date"
          }).valueDate
          let description = appr.entry[0].resource.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/description"
          })
          if(description) {
            this.supervisorApproval.description = description.valueString
          }
          this.getSupervisorSupervisorApproval()
        }
      }))
    },
    getSupervisorSupervisorApproval() {
      let query = {
        _profile: 'http://ihris.org/fhir/StructureDefinition/supervisor-supervisor-leave-approval-profile',
        leaverequest: 'Basic/' + this.queries.id,
        _sort: '_lastUpdated'
      }
      fetch("/fhir/Basic?" + querystring.stringify(query)).then(response => response.json().then((appr) => {
        if(appr.entry && appr.entry.length) {
          if(this.step != 4) {
            this.step=3
          }
          this.supervisorSupervisorApproval.id = appr.entry[0].resource.id
          this.supervisorSupervisorApproval.status = appr.entry[0].resource.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/supervisor-supervisor-leave-status"
          }).valueCoding.display
          this.supervisorSupervisorApproval.date = appr.entry[0].resource.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/action-date"
          }).valueDate
          let description = appr.entry[0].resource.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/description"
          })
          if(description) {
            this.supervisorSupervisorApproval.description = description.valueString
          }
          this.getHRApproval()
        }
      }))
    },
    getHRApproval() {
      let query = {
        _profile: 'http://ihris.org/fhir/StructureDefinition/hr-leave-approval-profile',
        leaverequest: 'Basic/' + this.queries.id,
        _sort: '_lastUpdated'
      }
      fetch("/fhir/Basic?" + querystring.stringify(query)).then(response => response.json().then((appr) => {
        if(appr.entry && appr.entry.length) {
          this.hrApproval.id = appr.entry[0].resource.id
          this.hrApproval.status = appr.entry[0].resource.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/hr-leave-status"
          }).valueCoding.display
          this.hrApproval.date = appr.entry[0].resource.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/action-date"
          }).valueDate
          let description = appr.entry[0].resource.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/description"
          })
          if(description) {
            this.hrApproval.description = description.valueString
          }
        }
      }))
    },
    createCarryForwardRequest() {
      this.overlayUnclosed = true;
      fetch("/site/leave/createCarryRequest/" + this.practitionerId, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        redirect: "manual",
        body: JSON.stringify(this.annualLeave),
      }).then((response) => {
        this.overlayUnclosed = false;
        this.carryForwardDialog = false
        if(response.status === 201) {
          this.$store.commit("setMessage", {
            type: "success",
            text: "Carry Forward Request Created Successfully",
          });
          response.json().then((response) => {
            this.getLeave()
            this.populateCarryForward(response)
          })
        } else {
          this.$store.commit("setMessage", {
            type: "error",
            text: "Failed To Create Carry Forward Request."
          });
        }
      }).catch(() => {
        this.overlayUnclosed = false;
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed To Create Carry Forward Request."
        });
      })
    },
    createCarryForwardAction() {
      this.overlayUnclosed = true;
      fetch("/site/leave/createCarryAction/" + this.carryForwardRequest.id + "/" + this.carryRequestAction, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        redirect: "manual"
      }).then((response) => {
        this.carryRequestAction = ""
        this.overlayUnclosed = false;
        this.carryForwardActionDialog = false
        if(response.status === 200) {
          this.$store.commit("setMessage", {
            type: "success",
            text: "Carry Forward Action Added Successfully",
          });
          response.json().then((response) => {
            this.populateCarryForward(response)
            this.getLeaveDetails()
          })
        } else {
          this.$store.commit("setMessage", {
            type: "error",
            text: "Failed To Add Carry Forward Action."
          });
        }
      }).catch(() => {
        this.overlayUnclosed = false;
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed To Add Carry Forward Action."
        });
      })
    }
  },
  created() {
    this.getLeave()
  },
  components: {
    "leave-request-stepper": () => import("./LeaveRequestStepper"),
  }
}
</script>